<template>
  <v-stepper v-model="e1">
    <camera-dialog/>

    <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :complete="e1 > n"
          :key="`${n}-step`"
          :step="n"
          editable
        >
          Step {{ n }}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >

        <v-card
          class="mb-5"
          color="grey lighten-1"
					height="60vh">
				</v-card>

        <v-btn
          color="primary"
          @click="showDialog()"
        >
          Dialog
        </v-btn>

        <v-btn
          color="primary"
          @click="nextStep(n)"
        >
          Continue
        </v-btn>

      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import Dialog from '@/components/Dialog.vue'
import { EventBus } from '@/eventBus.js'

export default {
  data () {
    return {
      e1: 1,
      steps: 9
    }
  },
  watch: {
    steps (val) {
      if (this.e1 > val) {
        this.e1 = val
      }
    }
  },
  methods: {
    onInput (val) {
      this.steps = parseInt(val)
    },
    nextStep (n) {
      if (n === this.steps) {
        this.e1 = 1
      } else {
        this.e1 = n + 1
      }
    },
  	showDialog() {
  		EventBus.$emit('dialog', true);
  	}

  },
  components: {
    cameraDialog: Dialog
  }
}
</script>