<template>
  <v-layout row justify-center>
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <!-- <v-btn slot="activator" color="primary" dark>Open Dialog</v-btn> -->
      <v-card>
        <v-toolbar>
          <v-btn icon @click.native="closeDialog()">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Take Picture</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn icon @click.native="closeDialog()">
              <v-icon>save</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <web-camera/>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import WebCamera from '@/components/WebCamera.vue'
import { EventBus } from '@/eventBus.js'

export default {
  data () {
    return {
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false
    }
  },
  created () {
    let vm = this
    EventBus.$on('dialog', function (value) {
      vm.dialog = value
    })
  },
  methods: {
    closeDialog() {
      EventBus.$emit('dialog', false);
    }

  },
  components: {
    WebCamera
  }
}
</script>